import {get,post} from './common';
/**
 * @param {String} url 请求地址
 * @param {Object} params 请求参数
 * @returns {Promise} 请求响应数据
 * @description GET请求
 */
export let getRequest = (url = '',params = {}) => {
    return new Promise((resolve,reject) => {
        get(url,params).then((result) => {
            resolve(result)
        }).catch((error) => {
            reject(error)
        });
    })
}
/**
 * @param  {String} url 请求地址
 * @param {Object} data 请求参数
 * @returns {Promise} 请求响应数据
 * @description POST请求
 */
export let postRequest = (url = '',data = {}) => {
    return new Promise((resolve,reject) => {
        post(url,data).then((result) => {
            resolve(result)
        }).catch((error) => {
            reject(error)
        });
    })
}
