import service from './service';
/**
 * @param {String} url 请求地址
 * @param params 请求参数
 * @returns {Promise} 请求响应数据
 * @description GET请求
 */
export let get = (url,params) => service({url,params,method:'GET'});
/**
 * @param {String} url 请求地址
 * @param {Object} data 请求参数
 * @returns {Promise} 请求响应数据
 * @description POST请求
 */
export let post = (url,data) => service({url,data,method:'POST'});